<template>
	<div id="fame">
    <div class="banner">
      <img src="/static/img/zh_head.png">  
    </div>
    <div class="title">
      
    </div>
    <div class="content">
       <div class="content_head">
        <img width="100%" src="/static/img/project_con_head1.png">
      </div>
      <div class="mian_head">
        <img class="l" src="/static/img/fame_main_h1.png" alt="">
        <img class="l" src="/static/img/fame_main_h2.png" alt="">
        <img class="l" src="/static/img/fame_main_h3.png" alt="">
        <div class="head_2">
          <img src="/static/img/fame_main_h4.png" alt="">
          <img src="/static/img/fame_main_h5.png" alt="">
        </div>
        <p>
          The One Club1960The One Club
The One ClubThe One Club<br><br>

2013122GothamThe One Club1984SteveBMWMartinNikeJimJohnRichard<br><br>

201212One ShowYou are the Creative StarVillageOne ShowCreative Hall of FameOne ShowThe One ClubOne ShowCreative Hall of Fame<br><br>

        </p>

        <!--  -->
        <div class="introduce_box" >
          <div class="intro_body" v-for="i in 3">
            <img @click="disc_ctrl(i)" src="/static/img/fame_body_1.png">
            <h4>David Abbott</h4>
             
          </div>
          <div class="clear"></div>
        </div>

      </div>
    </div>
    <mt-popup v-model="popupVisible1" position="top">
                <i @click="close()" class="iconfont icon-chuyidong">
                  
                </i>
                <div class="disc">  
                  <div class="disc_head">
                    <img src="/static/img/fame_body_1.png">
                    <div class="jianli">
                      <h3>David Abbott</h3>
                      <p>
                        Born 1938 <br>
                        Inducted : 1994<br>
                        Co-founderofChiat/ Day, New York<br>
                        Chiat/Day
                        </p>
                    	
                    </div>
                    <div class="clear"></div>
                  </div>
                  <div class="life_story">
                    <p>
                       AMV BBDO Mather &Crowther DDB 1966  DDB 1971  French Gold Abbott 1978 AMV 1991 BBDO  AMV  AMV BBDO  2001 The Upright Piano Player 2010  4  8 
                    </p>
                  </div>
                </div>
            </mt-popup>
          
	</div>
</template>
<script>
import { Popup } from 'mint-ui';
export default {
  name: 'fame',
  data() {
  	return {
      popupVisible1:false,
      layer : false
  	}
  },
  props: {},
  filters: {},
  created() {},
  methods: {
    disc_ctrl(i){
      console.log(i)
      if (!this.popupVisible1) {
        this.popupVisible1 = !this.popupVisible1
        
      }
    },
    close(){
      this.popupVisible1 = false;
     
    }

  },
  watch:{
    body(){
      if(this.popupVisible1){
        document.body.style.overflow = 'hidden';
      }else {
        document.body.style.overflow = 'auto';
      }
    }
  },
  components: {
  }
}
</script>
<style  lang="scss">
  #fame{
    .banner{
      height:135px;
      overflow: hidden;
      img{
        width:100%;
      }
    }
    .title{
      height: 40px;
      line-height: 40px;
      font-size: 18px;
      color: #020202;
      font-weight: bolder;
      background: #d3d3d3;
      padding:0 20px;
    }
    .content{
      padding: 40px 20px;
      h4{
        line-height: 35px;
        font-size: 18px;
        font-weight: bolder;
      }
      p{
        padding: 10px 0;
        line-height: 23px;
        font-size: 15px;
        color: #3c3c3c;
        font-family: "microsoft yahei";
        font-weight: 400;
      }
      .mian_head{
        margin-top: 30px;
        img{
          width:28%;
          margin-right: 5%;
        }
        .head_2{ 
          text-align: center;
          img{
            display: inline-block;
            margin: 2%;
          }
        }
      }
    }
    .introduce_box{
      width:100%;
      .intro_body{
        width:47%;
        float:left;
        min-height: 220px;
        img{
          display:block;
          width: 100%;
        }
        &:nth-child(2n){
          float:right;
        }
        
        
      }

    }
    .mint-popup-top{
          h4{
            line-height: 35px;
            font-size: 18px;
            font-weight: bolder;
          }
          p{
            padding: 10px 0;
            line-height: 23px;
            font-size: 17px;
            color: #3c3c3c;
            font-family: "microsoft yahei";
            font-weight: 400;
          }
          background:#000;
          height: 70%;
          width: 90%;
          top: 50%;
          transform:translate(-50%,-50%); 
          overflow:hidden;
          box-sizing: border-box;
          padding: 40px 15px 15px 15px;
         
          i{
            position:absolute;
            right: -0px;
            top: -6px;
            color: #fff;
            padding: 10px;
            z-index: 2000;
            font-size:30px;
          }
          .disc{
		    overflow: auto;
		    width: 100%;
		    height: 100%;
		    box-sizing: border-box;
		     padding-bottom:50px;
            img{
              width: 48%;
              box-sizing: border-box;
              float: left;
             
            }
            .jianli{
              float: left;
              width: 45%;
              padding-left:15px;
              p{
                padding: 0;
                font-size: 13px;
              }
            }

            h3{
              color:#fff;
            }
            p{
              padding: 20px 0px;
              color:#fff;
              font-size: 14px;
              font-weight: normal
            }
          }
        }
    .layer_box{
      z-index:10;
      position: fixed;
      background:rgba(0,0,0,0.5);
      width:100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
</style>


