<template>
  <div id="formUped">
      <form class="">
        <div class="user_body">
              <mt-radio
                  title=""
                  v-model="applyList.zhiwu"
                  :options="[{disabled: true,value:'1',label:''},{disabled: true,value:'0',label:''}]">
              </mt-radio>
              <mt-radio
                  title="ONE SHOW"
                  v-model="applyList.zhiyuan"
                  :options="[{disabled: true,value:'1',label:''},{disabled: true,value:'0',label:''}]">
              </mt-radio>
              <mt-radio
                  title="/ ()"
                  v-model="applyList.xiaoji"
                  :options="[{disabled: true,value:'1',label:''},{disabled: true,value:'0',label:''}]">
              </mt-radio>
              <div class="main">
                <label v-if="applyList.xiaoji==1" for="">
                  <textarea disabled="disabled"  v-model="applyList.xiaojimiaoshu" name="name">
                  </textarea>
                </label>
                <label for="">
                  <span></span>
                  <input disabled="disabled" type="file" name="" value="" >
                </label>
              </div>
              <mt-radio
                title="One Club China Workshop "
                v-model="applyList.workshop"
                  :options="[{disabled: true,value:'1',label:''},{disabled: true,value:'0',label:''}]">
              </mt-radio>
              <div class="main">
                <label v-if="applyList.workshop==1" for="">
                  <span><input disabled="disabled"  v-model="applyList.workshopYear" style="width:50px;margin-right:10px;display:inline-block" type="text" name="" value="">One Club China Workshop </span>
                  <br><br>
                </label>
              </div>
              <div class="main">
                <label for="">
                  <span>One Club China</span>
                  <textarea disabled="disabled"  v-model="applyList.jianyi" name="name">
                  </textarea>
                </label>
                <label for="">
                  <span>[ One Lectrue]</span>
                  <input disabled="disabled" type="file" name="" value="">
                </label>
                <label>
                  <span>/</span>
                </label>
              </div>
              <div class="daoshi">
                <label class="input">
                  <span></span>
                  <input disabled="disabled" v-model="applyList.teacher" type="text" name="" value="">
                </label>
                <mt-radio class="project"
                  title="TA"
                  v-model="applyList.teacherIs"
                  :options="[{disabled: true,'label':'','value':''},{disabled: true,'label':'','value':''},{disabled: true,'label':'','value':''},{disabled: true,'label':'','value':''},{disabled: true,'label':'','value':''},{disabled: true,'label':'','value':''}]">
                </mt-radio>
                <label class="input">
                  <span></span>
                  <input disabled="disabled" v-model="applyList.kecheng" type="text" name="" value="">
                </label>
                <label class="input">
                  <span>(/QQ)</span>
                  <input disabled="disabled" v-model="applyList.lianxi" type="text" name="" value="">
                </label>
                <mt-radio class="project"
                  title="TAOne Club China"
                  v-model="applyList.zhidao"
                  :options="[{disabled: true,value:'1',label:''},{disabled: true,value:'0',label:''}]">
                </mt-radio>
                <label class="input">
                  <span style="display:block" ></span>
                  <input disabled="disabled" v-model="mouth" style="margin: 10px;width:20%;margin-left:0" type="text" name="" value="">
                  <input disabled="disabled" v-model="year" style="margin: 10px;width:20%" type="text" name="" value="">
                </label>
              </div>
              <div class="main">
                <label for="">
                  <span></span>
                  <textarea disabled="disabled"  v-model="applyList.xiangshuo" name="name">
                  </textarea>
                </label>
                <label for="">
                  <span>ONE SHOW</span>
                  <input disabled="disabled" v-model="applyList.youjidizhi" style="width:100%;box-sizing:border-box;;margin-left:0" type="text" name="" value="">
                </label>
                <h3>* </h3>
                <label for="">
                  <span></span>
                  <input disabled="disabled" v-model="applyList.tuijianren" style="width:100%;box-sizing:border-box;;margin-left:0" type="text" name="" value="">
                </label>
                <h3></h3>
                <label for="">
                  <span></span>
                  <input disabled="disabled" v-model="applyList.tuijianrenShouji" style="width:100%;box-sizing:border-box;;margin-left:0" type="text" name="" value="">
                </label>
                <h3>*</h3>
              </div>
              <p>
                * One Club China<br>
                QQ/MSN<br>
                QQ/MSN<br>
                QQ/MSN<br>
              </p>
        </div>
      </form>
  </div>
</template>

<script>
import { Radio } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name:'formUped',
  data(){
    return {
      applyList:{
        'uid':'',
				'zhiwu':'',
				'zhiyuan':'',
				'xiaoji':'',
				'workshop':'',
				'zhidao':'',
				'teacherIs':'',
				'xiaojimiaoshu':'',
				'workshopYear':'',
				'jianyi':'',
				'teacher':'',
				'kecheng':'',
				'lianxi':'',
				'kaixue':'',
				'xiangshuo':'',
				'dateline':'',
				'youjidizhi':'',
				'tuijianren':'',
				'tuijianrenShouji':''
      },
      mouth:null,
      year:null
    }
  },
  created(){
    this.getForm()
  },
  methods :{
    getForm(){
      let tokenData = sessionStorage.token.split('/');
      let uid1 = tokenData[3]
      this.utils.ajax(this,'/app/ambassador',{uid:uid1},false).then((res)=>{
        if (res.data.sss == 1 || res.data.sss == 0 ) {
            this.applyList = res.data.Ambassador[0]
            this.year = this.applyList.kaixue.split('-')[0];
            this.mouth = this.applyList.kaixue.split('-')[1];
        }else if ( res.data.sss == 2 ) {

        }
      })
    }
  }
}
</script>

<style lang="scss">
#formUped{
  form{
    // 
    .user_body{
      padding-top: 20px;
      border-top:1px #d3d3d3 solid;
      p{
        margin-top:20px;
        color: #030303;
        font-size: 14px;
        line-height: 14px;
      }
      input{
        border-radius: 5px;
        ime-mode: disabled
      }
      .mint-radiolist-title{
        font-size: 14px;
        font-weight: bolder;
        color: #000;
        &:before{
          content: ' ';
          font-size: 16px;
          font-weight: bolder;
        }
      }
      .mint-radiolist-label{
        span{
          font-size: 12px;
        }
      }
      .mint-cell-wrapper{
        height: 30px;
      }
      .main{
        h3{
          font-size: 13px;
          color: #c00;
          font-weight: normal;
        }
        label{
          margin: 20px 0;
          span{
            font-size: 14px;
            font-weight: bolder;
            color: #000;
            &:before{
              content: ' ';
              font-size: 16px;
              font-weight: bolder;
            }
          }
          textarea{
            margin: 10px 0;
            border-radius: 5px;
            width: 100%;
            min-height:100px;
          }
          input{
            display: block;
            margin:10px 0;
            padding:5px 10px;
            margin-left: 10px;
            ime-mode: disabled
          }
        }
      }
      .daoshi{
        label.input{
          span{
            font-weight:bolder;
            display: inline-block;
            font-size: 13px;
            padding-left: 10px;
          }
          input{
            display: inline-block;
            margin:10px 0;
            padding:5px 10px;
            margin-left: 10px;
          }
        }
        .project{
          a{
            display : inline-block;
            width:auto;
          }
        }
      }
    }
    button{
      height: 35px;
      width:100px;
      background: #000;
      border: 0;
      color: #fff;
      display: block;
      margin: auto;
      margin-top: 20px;
    }
  }
}
</style>



