<template>
	<div id="apply">
		<div class="banner">
      <img src="/static/img/ambassador_banner.png">
    </div>
    <div class="title">
			<div class="title1000">
				
			</div>

    </div>
		<div class="content">
			<form class="" action="index.html" method="post">
				<div class="header">
					<div class="header_user">
							<div class="logo_box">
								<img :src="'http://www.oneshow.cn:8088'+face">
							</div>
							<div class="rest_form">
									<router-link to="/information"><button type="button" name="button"><br> </button></router-link>
							</div>
					</div>
					<div class="header_infor">
							<label class="cent"  for="">
								<span>:</span>
								<input  type="text" v-model="userList.username"  name="" value="">
							</label>
							<label class="cent"   for="">
								<span>:</span>
								<input type="text" v-model="userList.sex" name="" value="">
							</label>
							<label class="cent"  for="">
								<span>:</span>
								<input type="text" v-model="userList.school" name="" value="">
							</label>
							<label class="cent"  for="">
								<span>:</span>
								<input type="text" v-model="userList.department" name="" value="">
							</label>
							<label class=""  for="">
								<span>:</span>
								<input type="text" v-model="userList.city" name="" value="">
							</label>
							<label  for="">
								<span>:</span>
								<input type="text" v-model="userList.department" name="" value="">
							</label>
							<label  for="">
								<span>:</span>
								<input type="text" name="" v-model="userList.phone" value="">
							</label>
							<label  for="">
								<span>QQ:</span>
								<input type="text" v-model="userList.qq" name="" value="">
							</label>
							<label  for="">
								<span>E-mail:</span>
								<input type="text" v-model="userList.email" name="" value="">
							</label>
							<label  for="">
								<span>:</span>
								<input type="text"  v-model="userList.address"name="" value="">
							</label>
					</div>
				</div>
			</form>

			<!--  -->
				<form class=""  urlencode="multipart/form-data" v-if="status == 3 ">
						<div class="user_body">
									<mt-radio
										  title=""
										  v-model="applyList.zhiwu"
										  :options="[{value:'1',label:''},{value:'0',label:''}]">
									</mt-radio>
									<mt-radio
										  title="ONE SHOW"
										  v-model="applyList.zhiyuan"
										  :options="[{value:'1',label:''},{value:'0',label:''}]">
									</mt-radio>
									<mt-radio
										  title="/ ()"
										  v-model="applyList.xiaoji"
										  :options="[{value:'1',label:''},{value:'0',label:''}]">
									</mt-radio>
									<div class="main">
										<label v-if="applyList.xiaoji==1" for="">
											<textarea v-model="applyList.xiaojimiaoshu" name="name">
											</textarea>
										</label>
										<label for="">
											<span></span>
											<input type="text" name="" value="">
										</label>
										<label for="">
											<span></span>
											<input type="file" name="" value="" @change="change($event)">
										</label>
									</div>
									<mt-radio
									  title="One Club China Workshop "
									  v-model="applyList.workshop"
										  :options="[{value:'1',label:''},{value:'0',label:''}]">
									</mt-radio>
									<div class="main">
										<label v-if="applyList.workshop==1" for="">
											<span><input  v-model="applyList.workshopYear" style="width:50px;margin-right:10px;display:inline-block" type="text" name="" value="">One Club China Workshop </span>
											<br><br>
										</label>
									</div>
									<!-- <mt-radio
									  title="OneShow"
									  v-model="applyList.zhidao"
									  :options="[{value:'1',label:''},{value:'0',label:''}]">
									</mt-radio> -->
									<div class="main">
										<label for="">
											<span>One Club China</span>
											<textarea v-model="applyList.jianyi" name="name">
											</textarea>
										</label>
										<label for="">
											<span>[ One Lectrue]</span>
											<input type="file"  @change="change1($event)" name="" value="">
										</label>
										<label>
											<span>/</span>
										</label>
									</div>
									<div class="daoshi">
										<label class="input">
											<span></span>
											<input v-model="applyList.teacher" type="text" name="" value="">
										</label>
										<mt-radio class="project"
										  title="TA"
										  v-model="applyList.teacherIs"
										  :options="[{'label':'','value':''},{'label':'','value':''},{'label':'','value':''},{'label':'','value':''},{'label':'','value':''},{'label':'','value':''}]">
										</mt-radio>
										<label class="input">
											<span></span>
											<input v-model="applyList.kecheng"  type="text" name="" value="">
										</label>
										<label class="input">
											<span>(/QQ)</span>
											<input v-model="applyList.lianxi" type="text" name="" value="">
										</label>
										<mt-radio class="project"
										  title="TAOne Club China"
										  v-model="applyList.zhidao"
										  :options="[{value:'1',label:''},{value:'0',label:''}]">
										</mt-radio>
										<label class="input">
											<span style="display:block" ></span>
											<input v-model="mouth" style="margin: 10px;width:20%;margin-left:0" type="text" name="" value="">
											<input v-model="year" style="margin: 10px;width:20%" type="text" name="" value="">
										</label>
									</div>
									<div class="main">
										<label for="">
											<span></span>
											<textarea v-model="applyList.xiangshuo" name="name">
											</textarea>
										</label>
										<label for="">
											<span>ONE SHOW</span>
											<input v-model="applyList.youjidizhi" style="width:100%;box-sizing:border-box;;margin-left:0" type="text" name="" value="">
										</label>
										<h3>* </h3>
										<label for="">
											<span></span>
											<input v-model="applyList.tuijianren" style="width:100%;box-sizing:border-box;;margin-left:0" type="text" name="" value="">
										</label>
										<h3></h3>
										<label for="">
											<span></span>
											<input v-model="applyList.tuijianrenShouji" style="width:100%;box-sizing:border-box;;margin-left:0" type="text" name="" value="">
										</label>
										<h3>*</h3>
									</div>
									<p>
										* One Club China<br>
										QQ/MSN<br>
										QQ/MSN<br>
										QQ/MSN<br>
									</p>
						</div>
						<button type="submit" @click.prevent="postForm()" name="button"></button>
				</form>

				<!--  -->
				<div class="checkBox" v-if="status == 2">
					<h3></h3>
					<ul class="cardList clear">
						<li v-for="(card,i) in cardList">
							<img :src="'http://www.oneshow.cn:8088/auth/'+card.imgurl" v-if='light[i]'>
							<img :src="'http://www.oneshow.cn:8088/auth/'+card.imgurl2" v-if='!light[i]'>
						</li>
					</ul>
				</div>
				<!-- -->
				<div class="checkBox" v-if="status == 1">
						<h3>,<button class="top"  @click="check()"></button></h3>
						<formUped></formUped>
						<button class="remark" @click="check()" type="button" name="button"></button>
				</div>
				<!--  -->
				<div class="checkBox" v-if="status == 0">
						<h3><button  class="top" @click="check()"></button> </h3>
						<formUped></formUped>
						<button class="remark" @click="check()" type="button" name="button"></button>
				</div>
		</div>
	</div>
</template>
<script>
import formUped from '@/components/comment/formUped';
import { Radio } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'apply',
  data() {
  	return {
			file1:'',
			value:'',
			loginData:{
				'email':'',
				'password':''
			},
			userList:{
				'username':''
			},
			options1:[
					{
				    label: '',
				    value: '1'
				  },
				  {
				    label: '',
				    value: '0'
				  }
			],
			status:3,
			face:'',
			applyList:{
				'uid':'',
				'zhiwu':'',
				'zhiyuan':'',
				'xiaoji':'',
				'workshop':'',
				'zhidao':'',
				'teacherIs':'',
				'xiaojimiaoshu':'',
				'workshopYear':'',
				'jianyi':'',
				'teacher':'',
				'kecheng':'',
				'lianxi':'',
				'kaixue':'',
				'xiangshuo':'',
				'dateline':'',
				'youjidizhi':'',
				'tuijianren':'',
				'tuijianrenShouji':''
			},
			year:'',
			mouth:'',
			files:null,
			files2:null,
			statusMsg:'',
			cardList:null,
			userCard:null,
			light:[false,false,false,false,false,false,false,false]
  	}
  },
  props: {},
  filters: {},
  created() {
		this.getMine()
	},
  methods: {
		check(){
			this.status = 3
		},
		getMine(){
			if (sessionStorage.token== undefined) {
				if (!this.utils.userIf()) {
					this.$router.go(-1)
				}
				Toast({
					message: '',
					position: 'center',
					duration: 3000
				})
			}else {
				let tokenData = sessionStorage.token.split('/')
				this.loginData.email = tokenData[0];
				let length =  tokenData[2].length;
				this.applyList.uid = tokenData[3];
				this.loginData.password = tokenData[2].substring(0,length-2);
				this.$http.post(this.utils.baseUrl+'/login',this.loginData).then((res)=>{
						this.userList = res.data.data.userinfo
						this.face = this.userList.face
						if (this.userList.sex == 1) {
							this.userList.sex = ''
						}else if( this.userList.sex == 2 ){
							this.userList.sex = ''
						}
				})
				//     
				this.utils.ajax(this,'/app/ambassador',{uid:this.applyList.uid},false).then((res)=>{
					this.status = res.data.sss;
					if( res.data.sss == 0 || res.data.sss == 1 ){
						this.applyList = res.data.Ambassador[0]
						this.year = this.applyList.kaixue.split('-')[0];
	            		this.mouth = this.applyList.kaixue.split('-')[1];
					}else if ( res.data.sss == 2 ) {
						this.cardList = res.data.ambassadorCard;
						
						this.userCard = res.data.userCard
						
						for (let i = 0; i < this.cardList.length; i++) {
							for (let j = 0; j  < this.userCard.length; j++) {
								if(this.cardList[i].cardId == this.userCard[j].cardId ){
									this.light[i] = true;
								}
							}
						}
					}
				})
			}
		},
		change1(e){
			this.files2 = e.target.files[0];
			//console.log(e.target.files[0])
		},
		change(e) {
			this.files = e.target.files[0];
			//console.log(e.target.files[0])
		},
		postForm(){
			this.applyList.kaixue = this.year +'-'+ this.mouth;
			let fm = new FormData();
			for(let i in this.applyList){
				fm.append(i, this.applyList[i])
			}
			fm.append('jfile',this.files);
			fm.append('uid',this.applyList.uid)
			fm.append('cfile',this.files2)
			this.$http.post(this.utils.baseUrlApp+'/uploadAmbassadorInfo',fm).then((res)=>{
				let msg;
				if (res.body.data != 0 ) {
				 	msg= res.body.data.errmsg
				}else {
					msg= ''
				}
				Toast({
					message: msg,
					position: 'center',
					duration: 3000
				})
				setTimeout(()=>{
					this.$router.go(0);
				},3000)
			})
		}
	},
  components: {
		formUped
  }
}
</script>
<style  lang="scss">
 #apply{
	 .banner{
		 height: auto;
		 overflow: hidden;
		 img{
			 position: relative;
			 left: 50%;
			 transform: translateX(-50%);
			 height:140px;
		 }
		}
	 .title{
		 height: 40px;
		 line-height: 40px;
		 font-size: 18px;
		 color: #020202;
		 font-weight: bolder;
		 background: #d3d3d3;
		 padding:0 20px;
	 }
	 @media screen and(max-width:1000px){
	 	.title{
		 	margin-top: -6px;
	 	}
	 }
	 .content{
	 	padding: 30px;
		.checkBox{
			button.top{
				color: #fb6b6b
			}
			button.remark{
				width: 100px;
				color: #fff;
				background: #000;
				height:40px;
				display: block;
				margin:auto;
				margin-top: 30px;
			}
			h3{
				font-size: 16px;
				line-height: 50px;
				margin-bottom: 20px;
				color: #1eb7fb;
				&:first-child{
					text-align: center;
				}

			}
			.cardList{
				li{
					float: left;
					padding-bottom: 20px;
					margin-left: 10px;
				}
			}
		}
		form{
			.header{
				height: auto;
				padding-bottom: 20px;
				display: flex;
			}
			.header_user{
				width: 105px;
				.logo_box{
					width:105px;
					height: 105px;
					border: 1px #d3d3d3 solid;
					box-sizing: border-box;
					padding: 5px;
					display: flex;
					align-items: center;
					justify-content:center;
					img{
						max-width: 100%;
						max-height: 100%;
					}
				}
				.rest_form{
					margin-top:43px;
					width: 100%;
					button{
						width: 100%;
						background: #000;
						color: #fff;
						border: 0;
						height:47px;
						font-size: 13px;
					}
				}
			}
			// **************/
			.header_infor{
				flex:1;
				font-size: 13px;
				padding-left: 10px;

				label{
					margin-top:5px;
					width: 100%;
					display:inline-block;
					margin-bottom:16px;
					&.cent{
						width: 49%;
						input{
							width: 50%;
						}
					}
					span{
						font-weight:bolder;
						display: inline-block;
					}
					input{
						display:inline-block;
						width: 60%;
						border:0
					}
				}
			}
			// 
			.user_body{
				padding-top: 20px;
				border-top:1px #d3d3d3 solid;
				p{
					margin-top:20px;
					color: #030303;
					font-size: 14px;
					line-height: 14px;
				}
				input{
					border-radius: 5px;
				}
				.mint-radiolist-title{
					font-size: 14px;
					font-weight: bolder;
					color: #000;
					&:before{
						content: ' ';
						font-size: 16px;
						font-weight: bolder;
					}
				}
				.mint-radiolist-label{
					span{
						font-size: 12px;
					}
				}
				.mint-cell-wrapper{
					height: 30px;
				}
				.main{
					h3{
						font-size: 13px;
						color: #c00;
						font-weight: normal;
					}
					label{
						margin: 20px 0;
						span{
							font-size: 14px;
							font-weight: bolder;
							color: #000;
							&:before{
								content: ' ';
								font-size: 16px;
								font-weight: bolder;
							}
						}
						textarea{
							margin: 10px 0;
							border-radius: 5px;
							width: 100%;
							min-height:100px;
						}
						input{
							display: block;
							margin:10px 0;
							padding:5px 10px;
							margin-left: 10px;
						}
					}
				}
				.daoshi{
					label.input{
						span{
							font-weight:bolder;
							display: block;
							font-size: 13px;
							padding-left: 10px;
						}
						input{
							display: inline-block;
							margin:10px 0;
							padding:5px 10px;
							margin-left: 10px;
						}
					}
					.project{
						a{
							display : inline-block;
							width:auto;
						}
					}
				}
			}
			button{
				height: 35px;
				width:100px;
				background: #000;
				border: 0;
				color: #fff;
				display: block;
				margin: auto;
				margin-top: 20px;
			}
		}
	 }
	 @media screen and (min-width:1000px){
	 	.title1000{
	 		width: 1000px;
			margin: auto;
	 	}
		.banner{
			height: 249px;
			img{
				height: 100%;
				width: auto;
			}
		}
	 	.content{
	 		width: 1000px;
			box-sizing: border-box;
			padding-right: 290px;
			margin: auto;
			form{
				.header{
					width: 500px;
				}
			}
	 	}
	 }
 }
</style>



