// 
import index from '@/components/index/index'
import home_index from '@/components/index/home-index'
import home_mine from '@/components/index/home-mine'
import password from '@/components/index/password'
import information from '@/components/index/information'
import userlogo from '@/components/index/userlogo'
import listEnter from '@/components/index/listEnter'
import artical from '@/components/index/comment'
import privacy from '@/components/index/privacy'
import zfubaoPay from '@/components/comment/zfubaoPay'
import instruction from '@/components/index/instruction'
export default {
	path: '/',
	meta: {
		requiresAuth: true,
		title:''
	},
	component: index,
	children:[
		{
			path:'/privacy',
			name: 'privacy',
			meta:{
				title:"OneShow-",
			},
			component:privacy
		},
		{
			path:'/instruction',
			name: 'instruction',
			meta:{
				title:"OneShow-",
			},
			component:instruction
		},
		{
			path:'/',
			name: 'home_index',
			meta:{
				title:"OneShow",
			},
			component:home_index
		},
		{
			path:'/zfubaoPay',
			name: 'zfubaoPay',
			meta:{
				title:"",
			},
			component:zfubaoPay
		},
		{
			path:'/artical/:id',
			name: 'artical',
			meta:{
				title:"",
			},
			component:artical
		},
		{
			path:'/mine',
			name: 'home_mine',
			meta:{
				title:"",
				requiresAuth:true
			},
			component:home_mine

		},
		{
			path:'/password',
			name: 'password',
			meta:{
				title:"",
				requiresAuth:true
			},
			component:password
		},
		{
			path:'/information',
			name: 'information',
			meta:{
				title:"",
				requiresAuth:true
			},
			component:information
		},
		{
			path:'/userlogo',
			name: 'userlogo',
			meta:{
				title:"",
				requiresAuth:true
			},
			component:userlogo
		},
		{
			path:'/listEnter',
			name: 'listEnter',
			meta:{
				title:"",

			},
			component:listEnter
		},

	]
}



